@use '../../common/styles/mixins' as *;

.look-view {
  width: 25%;
  margin-right: 1rem;

  &__figure {
    width: 100%;
    padding-bottom: 120%;
    position: relative;
    margin: 0;
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__footer {
    display: none;
  }

  &__title {

  }

  @include respond(mid) {
    width: 60%;
  }
}