@use '../../common/styles/mixins' as *;

.shop-the-look-section {
  padding: 5rem 0;

  @media (max-width: 1140px) {
    padding: 1.75rem 0;
  }

  @include respond(tablet) {
    min-height: 61.5rem;
  }

  @include respond(mid) {
    min-height: 70vw;
  }

  .stl--title,
  .stl--subtitle {
    text-align: center;
    // font-size: calc(0.5em + 0.5vw);
    line-height: 1.1em;
    word-break: break-word;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    // font-family: 'Monotype News Gothic', sans-serif;
  }

  .stl--subtitle {
    margin-top: 0.5rem;
    margin-bottom: 1.25rem;
    font-weight: 500;
  }
  .stl--title {
    letter-spacing: 0 !important;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0;
    margin-bottom: 0.5rem;
    // font-size: calc(1em + 0.5vw);
    font-weight: 600;
  }

  .flickity-slider {
    cursor: pointer;
  }

  .products-slider-wrapper {
    display: flex;
    @media (max-width: 1140px) {
      padding: 1.25rem;
      flex-direction: column;
    }
    .hotspot {
      position: absolute;
      display: block;
      width: 16px;
      height: 16px;
      background: #ffffff;
      border-radius: 100%;
      box-shadow: 0 1px 10px rgba(0, 0, 0, 0.25);
      cursor: pointer;
      animation: 1.4s pulse ease-in-out infinite;

      &::before {
        content: '';
        position: absolute;
        width: 2.5rem;
        height: 2.5rem;
        left: -0.75rem;
        top: -0.75rem;
        border-radius: 100%;
        background: rgba(255, 255, 255, 0.4);
      }
    }
    .active-hotspot {
      transform: scale(1.5);
      animation: 1.4s active-pulse ease-in-out infinite;
    }
    .text-block {
      width: 27.5%;
      display: flex;
      justify-content: center;
      text-align: center;
      align-items: center;
      flex-direction: column;
      @media (max-width: 1140px) {
        width: 100%;
        order: 10;
        margin-top: 15px;
      }
      h2 {
        margin: 0.5rem 0;
      }
    }
    .products-slider {
      width: 72.5%;
      overflow: hidden;
      transition: transform 300ms linear;
      @media (max-width: 1140px) {
        width: 100%;
      }
      .swiper-container {
        transition: transform 300ms linear, transform-origin 300ms linear;
        transform-origin: top;
        height: 47vw;
        @media (max-width: 1140px) {
          height: 122vw;
        }
      }
      .swiper-slide {
        height: 100%;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        &:hover {
          cursor: pointer;
        }
      }
    }
  }
  .products-slider-modal {
    z-index: 999;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    .overlay {
      background: rgba(255, 255, 255, 0.5);
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }
    .modal-card {
      max-width: 900px;
      margin: 0 auto;
      background: white;
      position: relative;
      box-shadow: 0 2px 10px rgba(54, 54, 54, 0.15);

      .close-button {
        z-index: 10;
        position: absolute;
        cursor: pointer;
        right: 1rem;
        top: 1rem;
      }
      .swiper-container {
        width: 100%;
        height: 100%;
        .swiper-slide {
          width: 900px;
          .slide-content {
            width: 100%;
            height: 100%;
            display: grid;
            gap: 1.25rem;
            grid-template-columns: repeat(2, minmax(0, 1fr));

            .image-container {
              position: relative;
              overflow: hidden;
              grid-column: 1 / span 1;
              img {
                display: block;
                width: 100%;
                min-height: 400px;
                object-fit: cover;
              }
            }
            .slp-product-details-wrapper {
              position: relative;
              grid-column: 2 / span 1;
              overflow-y: auto;
            }
            .slp-product-details {
              position: absolute;
              top: 0;
              left: 0;
              right: 0;
              bottom: 0;
              padding: 1rem 2rem 1rem 0;

              .options-selects-wrapper {
                position: relative;
                display: flex;
                flex-wrap: wrap;
                background: #ebebeb;

                .options-select {
                  padding: 0 0 0.25rem;
                  line-height: 1.3;
                  .toggle-dropdown-button {
                    padding: 0 0.5rem;
                    cursor: pointer;
                    font-size: 0.6rem;
                    text-transform: uppercase;
                    svg {
                      height: 12px;
                      width: 12px;
                      display: inline-block;
                      vertical-align: middle;
                      margin-left: 0.1rem;
                    }
                  }
                  .dropdown {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    grid-template-columns: repeat(2, minmax(0, 1fr));
                    background: white;
                    box-shadow: 0 -2px 10px rgba(54, 54, 54, 0.2);
                    .dropdown-item {
                      text-transform: uppercase;
                      font-size: 0.65rem;
                      padding: 0.75rem;
                      cursor: pointer;
                      &:hover {
                        background-color: #f3f3f3;
                      }
                      text-align: center;
                      .dropdown-item--text {
                        display: inline-block;
                        vertical-align: middle;
                      }
                    }
                  }
                }
              }
              .quick-product-wrapper {
                display: flex;
                flex-direction: row;
                gap: 1.5rem;
                padding-bottom: 1rem;

                .product-image-wrapper {
                  min-width: 140px;
                  .product-image-container {
                    position: relative;
                    &::before {
                      content: '';
                      display: block;
                      width: 100%;
                      padding-bottom: 140%;
                    }
                    img {
                      position: absolute;
                      top: 0;
                      left: 0;
                      height: 100%;
                      width: 100%;
                      object-fit: cover;
                    }
                  }
                }
                .product-details {
                  display: flex;
                  flex-direction: column;
                  width: 100%;
                  .quick-product--title {
                    font-size: 0.8rem !important;
                    margin: 0.625rem 0 0 0;
                    letter-spacing: 0 !important;
                    font-weight: 400 !important;
                    line-height: 1.4em !important;
                    color: inherit;
                    text-decoration: none;
                    padding-right: 0.5rem;
                    a {
                      text-decoration: none;
                    }
                  }
                  .quick-product--price {
                    letter-spacing: 0 !important;
                    font-size: 0.8rem !important;
                    line-height: 1.4em !important;
                    font-weight: 300;
                  }
                  .quick-product--compare-price {
                    margin-left: 0.5rem;
                    text-decoration: line-through;
                    letter-spacing: 0.2em;
                    font-size: 0.8rem !important;
                    line-height: 1.4em !important;
                  }
                }
              }
            }
          }
        }
      }
      .swiper-controls {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        height: 2rem;
        width: 2rem;
        padding: 0.25rem;
        border-radius: 50%;
        background: white;
        box-shadow: 0 1px 10px rgba(0, 0, 0, 0.25);
        cursor: pointer;
        svg {
          display: inline-block;
        }
      }
      .swiper-next-btn {
        right: -5rem;
      }
      .swiper-prev-btn {
        left: -5rem;
      }
    }
  }
  .view-product-btn {
    margin-top: 1.5rem;
  }
}
.products-slider-modal--mobile {
  z-index: 9999999;
  position: fixed;
  display: flex;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50vh;

  .card {
    width: 100%;
    min-height: 100%;
    height: 100%;
    margin: 0 auto;
    background: white;
    position: relative;
    box-shadow: 0 -2px 10px rgba(54, 54, 54, 0.4);
    overflow: hidden;
    .card-header {
      padding: 1.35rem 2.25rem;
      border-bottom: 1px solid rgba(221, 221, 221, 0.4);
      background: #fff;
      width: 100%;
      position: relative;
      h2 {
        margin: 0;
        text-align: center;
        letter-spacing: 0.2em;
        text-transform: uppercase;
        font-size: 1.2rem;
        // font-family: 'Monotype News Gothic', sans-serif;
      }
    }
    .close-button {
      left: 0.75rem;
      top: 50%;
      transform: translateY(-50%);
      position: absolute;
      display: flex;
      height: max-content;
      z-index: 2;

      svg {
        width: 21.5px;
        height: 21.5px;
      }
    }
    .swiper-container {
      height: 100%;
      .slide-content {
        height: 100%;
        .image-container {
          width: 100%;
          height: 100%;
          overflow: hidden;
          position: relative;
          background: #f4f3f8;
          img {
            object-fit: cover;
            object-position: 50% 50%;
            width: 100%;
            height: 100%;
          }
        }
        a {
          display: block;
          position: relative;

          @media only screen and (min-width: 500px) and (min-height: 700px) {
            display: initial;

            img { object-fit: contain !important; }
          }

          @media (max-height: 750px) and (max-width: 375px) {
            top: -3.5vw;
          }

          @media (max-height: 750px) and (max-width: 360px) {
            top: -6.5vw;
          }

          @media (min-height: 800px) and (max-width: 400px) {
            top: -2.75vw;
          }
        }
        .product-title {
          max-width: 80%;
          margin: 0.5rem auto;
          font-size: 0.75rem;
          letter-spacing: 0.2em;
          color: black;
        }
      }
    }
  }
}
.product--price {
  letter-spacing: 0 !important;
  font-size: 0.8rem !important;
  line-height: 1.4em !important;
}
.product--compare-price {
  margin-left: 0.5rem;
  text-decoration: line-through;
  letter-spacing: 0.2em;
  font-size: 0.8rem !important;
  line-height: 1.4em !important;
}
.btn-primary {
  position: relative;
  display: inline-block;
  padding: 14px 28px;
  line-height: normal;
  border: 1px solid transparent;
  border-radius: 0;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.2em;
}
.shop-the-look-section .btn-add-to-cart {
  display: inline-block;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  z-index: initial;
  padding: 8px 8px;
  width: 100%;
  max-width: 200px;
  // font-size: 0.5rem !important;
  cursor: pointer;
  line-height: normal;
  border: 1px solid transparent;
  border-radius: 0;
}

.btn-black {
  color: #fff !important;
  border-color: #000 !important;
  background-color: black !important;
}

.transition-slide-from-bottom {
  transition: transform 300ms ease-in-out;
}
.translate-from-bottom {
  transform: translateY(100%);
}
.translate-to-bottom {
  transform: translateY(0);
}

.transition-card {
  transition: transform 300ms ease-in-out, opacity 300ms ease-in-out;
}
.transition-card-from {
  opacity: 0;
  transform: translateY(10%);
}
.transition-card-to {
  opacity: 1;
  transform: translateY(0);
}

.transition-opacity {
  transition: opacity 222ms minear;
}
.transition-opacity-from {
  opacity: 0;
}
.transition-opacity-to {
  opacity: 1;
}

.spacer {
  flex-grow: 1;
}

@keyframes pulse {
  50% {
    transform: scale(1.1);
  }
}
@keyframes active-pulse {
  50% {
    transform: scale(1.65);
  }
}

.modalfs {
  background: #fff;
  z-index: 99999999;
  transition: all 0.5s ease;
  display: flex;
}

.modalfs__backdrop,
.modalfs {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  animation: fadeIn 0.45s ease;
  animation-fill-mode: backwards;
}

.modalfs--transition {
  transform: translateY(100%);
  opacity: 0;
}

.modalfs--transition * {
  visibility: hidden;
}

.modalfs--transition + .modalfs__backdrop {
  opacity: 0;
}

.modalfs__backdrop {
  transition: opacity 0.75s ease;
  transition-delay: 0.35s;
  z-index: 99999998;
  background: rgba(0, 0, 0, 0.75);
}

.modalfs__left {
  width: 60%;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
}

.modalfs__head {
  padding: 40px;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  padding-bottom: 0;
}

.modalfs__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #a7a7a7;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.5px;
}

.modalfs__link {
  display: block;
  width: 100%;
  height: 100%;
}

.modalfs__item:not(:last-child) {
  margin-bottom: 10px;
}

.modalfs__icon-arrow {
  margin-right: 10px;
  width: 20px;
  height: 20px;
  stroke: #a7a7a7;
}

.modalfs__item--active {
  color: #000;
}

.modalfs__item--active svg {
  stroke: #000;
}

.modalfs__group {
  display: flex;
  cursor: pointer;
  align-items: center;
}

.modalfs__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.modalfs__btn-slide {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modalfs__btn-slide--inactive {
  opacity: 0.5;
}

.modalfs__btn-slide:not(:last-child) {
  margin-right: 5px;
}

.modalfs__controls {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 40px 0;
  animation: fadeIn 0.2s ease 1.25s;
  animation-fill-mode: backwards;
}

.modalfs__body {
  height: 30vw;
  animation: slideFromRight 0.25s cubic-bezier(0.84, 0.56, 0.27, 0.6) 0.675s;
  animation-fill-mode: backwards;
}

.modalfs__body .swiper-slide {
  background: #f4f3f8;
}

.modalfs__body .modalfs__img {
  animation: fadeIn 0.3s ease 1.25s;
  animation-fill-mode: backwards;
}

.modalfs__slider {
  height: 100%;
}

.modalfs__right {
  position: relative;
  z-index: 2;
  width: 40%;
  background: #f4f3f8;
  animation: slideFromRight 0.6s cubic-bezier(0.84, 0.56, 0.1, 0.85) 0.35s;
  animation-fill-mode: backwards;
}

.modalfs__btn {
  position: absolute;
  z-index: 3;
  top: 0;
  right: 0;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalfs--featured-left .modalfs__body {
  animation: slideFromLeft 0.25s cubic-bezier(0.84, 0.56, 0.27, 0.6) 0.675s;
  animation-fill-mode: backwards;
}

.modalfs--featured-left .modalfs__right {
  order: -1;
  animation: slideFromLeft 0.75s cubic-bezier(0.84, 0.56, 0.1, 0.85) 0.35s;
  animation-fill-mode: backwards;
}

.modalfs--featured-left .modalfs__btn {
  left: 0;
  right: initial;
}

.modalfs__body .swiper-container-free-mode > .swiper-wrapper,
.products-slider-wrapper .swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: linear;
}

@media screen and (max-width: 1024px) {
  .modalfs__head {
    display: none;
  }

  .modalfs--featured-left .modalfs__btn {
    left: initial;
    right: 0;
  }

  .modalfs__right {
    display: none;
  }

  .modalfs__body {
    height: 100vh;
    animation: none;
  }

  .modalfs__left {
    width: 100vw;
  }
}

@keyframes slideFromLeft {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideFromRight {
  from {
    opacity: 0;
    transform: translateX(100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.slider-image__wrapper {
  width: 40%;
  margin-right: 4px;
  .slider-image__container {
    position: relative;
    width: 100%;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &::after {
      content: '';
      display: block;
      padding-bottom: 140%;
    }
  }
}

.stl {
  &__swiper { position: relative; }

  &__icon {
    width: 25px;
    height: 25px;
    fill: #5e5e5e;
  }

  &__btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1; 
    transition: opacity .4s ease;
  
    &--next { right: 5px; }
    &--prev { left: 5px; }
    &--disabled { opacity: .45; }
  }
}

.active-slide--mobile  {
  .stl__btn {
    visibility: hidden;
    opacity: 0;
  }

  .swiper-slide {
    background: #f4f3f8;
  }
}

.image-cover {
  object-fit: cover;
  object-position: top;
}
.image-contain {
  object-fit: contain !important;
  object-position: initial !important;
  height: 90% !important;
}