@use '../../common/styles/mixins' as *;

.looks {
  padding-top: 4rem;

  &__product-item {
    width: 25%;
    margin-right: .35rem;

    .product-item--layout_2 {
      .wishlist-engine {
        margin-top: -3px;
        margin-left: -5px;
      }
    }

    @include respond(mid) {
      width: calc(50% - .175rem);
      margin-right: 0;

      .product-item__footer {
        margin-top: .25rem;
        min-height: initial;
        padding: 0 .5rem;
        padding-bottom: 3rem;
      }
    }

    .product-item__body.active {
      .product-item__media {
        @media screen and (max-width: 1300px) {
          top: calc(var(--options-size) * 8%);
        }
        @media screen and (max-width: 1160px) {
          top: calc(var(--options-size) * 10%);
        }
        @media screen and (max-width: 1030px) {
          top: calc(var(--options-size) * 12%);
        }
        @media screen and (max-width: 860px) {
          top: calc(var(--options-size) * 14%);
        }
        @media screen and (max-width: 770px) {
          top: calc(var(--options-size) * 10%);
        }
        @media screen and (max-width: 690px) {
          top: calc(var(--options-size) * 12%);
        }
        @media screen and (max-width: 590px) {
          top: calc(var(--options-size) * 14%);
        }
        @media screen and (max-width: 510px) {
          top: calc(var(--options-size) * 16%);
        }
        @media screen and (max-width: 482px) {
          top: calc(var(--options-size) * 13%);
        }
        @media screen and (max-width: 400px) {
          top: calc(var(--options-size) * 16%);
        }
      }
    }
  }
  
  &__head {
    text-align: center;
    margin-bottom: 4rem;

    h2 {
      font-size: 2.4rem;
      margin-top: 0;
      margin-bottom: .5rem;
    }

    a { font-size: 1.4rem; }
  }

  &__body {
    display: flex;
    overflow: hidden;
    & > * { flex-shrink: 0; }

    &--wrapper {
      position: relative;
    }
    &--hover-navigation {
      position: absolute;
      top: 0;
      width: calc(12% - 1rem);
      height: 100%;
      z-index: 2;
    }
    &--next {
      right: 0;
    }
    &--prev {
      left: 0;
    }
  }

  &__content {
    position: relative;
    justify-content: center;
    width: 100%;
    max-height: 50vw;
    display: flex;

    @include respond(mid) {
      max-height: none;
    }
  }

  &__products {
    padding: 5rem 0;
    padding-top: 7rem;
    width: 80%;
    opacity: 0;
    visibility: hidden;
    transition: opacity .4s ease;
    display: flex;
    justify-content: center;

    &--hover-navigation {
      position: absolute;
      top: 7rem;
      width: calc(10% - .5rem);
      height: calc(100% - 12rem);
      z-index: 2;
    }

    &--next {
      right: 10%;
    }

    &--prev {
      left: 10%;
    }

    @include respond(mid) {
      width: 60%;
      display: flex;
      flex-wrap: wrap;
      column-gap: .35rem;
      align-content: start;
    }

    @include respond(sm) {
      width: 80%;
    }
  }

  &__close-btn {
    position: absolute;
    top: 3.5rem;
    right: 2rem;
    z-index: 3;

    svg {
      width: 1.8rem;
      height: 1.8rem;
      color: #000;
      fill: #000;
    }

    @include respond(sm) {
      svg {
        width: 1.35rem;
        height: 1.35rem;
      }
    }
  }

  &.active &__close-btn svg {
    animation: rotate .4s ease;
    animation-delay: .25s;
  }

  &.active &__products {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes rotate {
  from { transform: rotate(-180deg); }
  to { transform: rotate(0); }
}