.section-container-small {
  margin-left: 12rem;
  margin-right: 12rem;
}

.section-container-big-stretch {
  margin-left: 0;
  margin-right: 0;
}

.section-wrapper {
  height: 0;
  position: relative;
  padding-bottom: 56.25%;
  overflow: hidden;

  background-size: cover;
  background-position: center;

  .section-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
  }
}

.section-wrapper::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.section-container-small .section-wrapper {
  padding-bottom: 56.25%;
}

.section-container-full .section-wrapper {
  padding-bottom: 177.77%;
  @media screen and (min-width: 750px) {
    padding-bottom: 56.25%;
  }
}

// .section-container-full_top .section-wrapper {
//   padding-bottom: calc(100vh - var(--announcement-banner-height, 30px))!important
// }

.section-wrapper video { 
  position: absolute; 
  top: 0; 
  left: 50%;
  z-index: 1;
  width: 100%;
  height: 100%;
  transform: translateX(-50%); 
  background-size: cover;
}

.section-container-full .section-wrapper video {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  width: 100%;
  height: 100%;
  transform: translate(-50%, -50%);
}

.image-video-content {
  position: absolute;
  z-index: 2;
  width: 100%;  
  display: flex;
  flex-direction: column;
}

.image-video-content .text-small,
.image-video-content .text,
.image-video-content .text-large {
  max-width: 65%;
}

.image-video-content h2,
.image-video-content .text-small,
.image-video-content .text,
.image-video-content .text-large {
  color: #fff;
  margin-top: 0.8rem;
  margin-bottom: 0.8rem;
}

/* Video Controllers */

.ButtonGroup {
  display: flex;
  gap: 2.4rem;
  margin-top: 2rem;
}

.ButtonGroup--controlers {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 4rem;
  z-index: 4;
  justify-content: space-between;
}

.ButtonGroup--left {
  &.ButtonGroup--playToggle {
    left: 4rem;
    right: initial;
  }
  &.ButtonGroup--muteToggle {
    left: 4rem;
    right: initial;
    transform: translateX(100%);
  }
  @media screen and (max-width: 750px) {
    &.ButtonGroup--playToggle {
      left: 1.5rem;
    }
    &.ButtonGroup--muteToggle {
      left: 1.5rem;
    }
  }
}

.ButtonGroup--right {
  &.ButtonGroup--playToggle {
    right: 4rem;
    left: initial;
    transform: translateX(-100%);
  }
  &.ButtonGroup--muteToggle {
    right: 4rem;
    left: initial;
  }
  @media screen and (max-width: 750px) {
    &.ButtonGroup--playToggle {
      right: 1.5rem;
    }
    &.ButtonGroup--muteToggle {
      right: 1.5rem;
    }
  }
}

.ButtonGroup--center {
  &.ButtonGroup--playToggle {
    left: 4rem;
  }
  &.ButtonGroup--muteToggle {
    right: 4rem;
  }
  @media screen and (max-width: 750px) {
    &.ButtonGroup--playToggle {
      left: 1.5rem;
    }
    &.ButtonGroup--muteToggle {
      right: 1.5rem;
    }
  }
}

.ButtonGroup--Btn  {
  border: none;
  background: transparent;
  cursor: pointer;
  position: absolute;
  bottom: 4rem;
  z-index: 4;

  @media screen and (max-width: 750px) {
    bottom: 2rem;
  }
}

.ButtonGroup--Btn svg { 
  width: 25px;
  height: 25px;
  color: #fff;
  fill: #fff;
  stroke: #fff;
  display: inline-block;
}

.ButtonGroup--playToggle {
  &[data-state=playing] {
    svg[data-type=resume] {display: none;}
    svg[data-type=pause] {display: initial;}
  }

  &[data-state=paused] {
    svg[data-type=resume] {display: initial;}
    svg[data-type=pause] {display: none;}
  }
}

.ButtonGroup--muteToggle {
  &[data-state=muted] {
    svg[data-type=unmute] {display: none;}
    svg[data-type=mute] {display: initial;}
  }

  &[data-state=unmuted] {
    svg[data-type=unmute] {display: initial;}
    svg[data-type=mute] {display: none;}
  }
}

@media screen and (max-width: 750px) {
  .section-container-small {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .section-container-big {
    margin-left: 0;
    margin-right: 0;
  }

  .section-container-big-stretch {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .section-container-full {
    margin-left: 0;
    margin-right: 0;
  }

  .section-container-big .section-wrapper,
  .section-container-big-stretch .section-wrapper
   {
    height: auto;
    padding-bottom: 177%;
  }

  .section-container-small .section-wrapper {
    padding-bottom: 166%;
  }

  .section-wrapper video {
    top: 1px; 
    left: 50%;
    transform: translateX(-50%);
    width: max(350%, 250vh);  
  }

  .ButtonGroup--controlers {
    bottom: 2rem;
  }

  .section-container {
    padding: 4rem 2rem;
  }

  .section-container-full {
    margin: 0;
  }

  .image-video-content .Button:not(:first-child) {
    display: none;
  }
}

@media screen and (max-width: 850px) {
  .image-video-content .text-small,
  .image-video-content .text,
  .image-video-content .text-large {
    max-width: 85%;
  }
  .ButtonGroup {
    margin-top: 1rem  
  }
} 

@media screen and (max-width: 500px) {
  .ButtonGroup {
    flex-direction: column;
    gap: 1.6rem;
  }
} 