.media--small.media-text-section--container,
.media--medium.media-text-section--container {
  padding-left: 15rem;
  padding-right: 15rem;
}

.media--large.media-text-section--container {
  padding-left: 5rem;
  padding-right: 5rem;
}

.media--large-vertical.media-text-section--container {
  padding-left: 15rem;
  padding-right: 15rem;
}


.media-text-section--wrapper {
  display: flex;
  align-items: center;
  gap: 10rem;
}

.media--small .media-container {
  width: 40%;
}

.media--small .text-content-wrapper {
  width: 60%;
}

.media--medium .media-container {
  width: 50%;
}
.media--medium .text-content-wrapper {
  width: 50%;
}

.media--large .media-container {
  width: 70%;
}

.media--large .text-content-wrapper {
  width: 30%;
}

.media-container {
  position: relative;
  .section-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
  }
}


.media-wrapper {
  position: relative;
  overflow: hidden;

  background-size: cover;
  background-position: center;
}

.media--small .media-wrapper,
.media--medium .media-wrapper {
  padding-bottom: 125%;
}

.media--large .media-wrapper {
  padding-bottom: 80%
}

.media-wrapper video {
  position: absolute;
  top: 50%;
  left: 50%;
  width: max(250%, 250vh);
  height: 100%;
  transform: translate(-50%, -50%);
}

.section-reversed {
  flex-direction: row-reverse;
}

.media--small .section-ordered .text-content-wrapper,
.media--medium .section-ordered .text-content-wrapper {
  padding-left: 8rem;
}

.media-wrapper .ButtonGroup--controlers {
  bottom: 2rem;
}
.media-wrapper {
  .ButtonGroup--Btn {
    bottom: 2rem;
    @media screen and (max-width: 750px) {
      bottom: 1.5rem;
    }
  }

  .ButtonGroup--left {
   &.ButtonGroup--playToggle {
     left: 2rem;
     right: initial;
   }
   &.ButtonGroup--muteToggle {
     left: 2rem;
     right: initial;
     transform: translateX(100%);
   }
   @media screen and (max-width: 750px) {
     &.ButtonGroup--playToggle {
       left: 1.5rem;
     }
     &.ButtonGroup--muteToggle {
       left: 1.5rem;
     }
   }
 }
  
 .ButtonGroup--right {
   &.ButtonGroup--playToggle {
     right: 2rem;
     left: initial;
     transform: translateX(-100%);
   }
   &.ButtonGroup--muteToggle {
     right: 2rem;
     left: initial;
   }
   @media screen and (max-width: 750px) {
     &.ButtonGroup--playToggle {
       right: 1.5rem;
     }
     &.ButtonGroup--muteToggle {
       right: 1.5rem;
     }
   }
 }
 
 .ButtonGroup--center {
   &.ButtonGroup--playToggle {
     left: 2rem;
   }
   &.ButtonGroup--muteToggle {
     right: 2rem;
   }
   @media screen and (max-width: 750px) {
     &.ButtonGroup--playToggle {
       left: 1.5rem;
     }
     &.ButtonGroup--muteToggle {
       right: 1.5rem;
     }
   }
 }
}


.media--large-vertical .media-text-section--wrapper {
  flex-direction: column;
}

.media--large-vertical .media-text-section--wrapper.section-reversed  {
  flex-direction: column-reverse;
}

.media--large-vertical .text-content-wrapper {
  text-align: center;
  width: 70%;
}

.media--large-vertical .text-content-wrapper h2 {
  margin-top: 0;
}

.media--large-vertical .text-content-wrapper .text-small
.media--large-vertical .text-content-wrapper .text,
.media--large-vertical .text-content-wrapper .text-large {
  max-width: 65%;
  margin: 0 auto;
}

.media--large-vertical .text-content-wrapper .Button {
  margin-top: 1.5rem;
  display: inline-block;
}

.text-content-wrapper .Button {
  margin-top: 1.5rem;
  display: inline-block;
}

.text-content-wrapper .text-small
.text-content-wrapper .text
.text-content-wrapper .text-large {
  margin-bottom: 0;
}

.media--large-vertical .media-wrapper {
  padding-bottom: 56.25%;
}

.media--large-vertical .media-container {
  width: 80%;
}

.media--large-vertical .media-text-section--wrapper {
  gap: 3rem!important;
}



@media screen and (max-width: 1140px) {
  .section-ordered.media-text-section--wrapper {
    gap: 0;
  }
  .media--large .section-ordered.media-text-section--wrapper {
    gap: 5rem;
  }
  .section-reversed {
    gap: 5rem;
  }

  .media--small.media-text-section--container, 
  .media--medium.media-text-section--container {
    padding-left: 5rem;
    padding-right: 5rem;
  }

  .media--large-vertical .media-container {
    width: 100%;
  }

  .media--large-vertical.media-text-section--container {
    padding-left: 10rem;
    padding-right: 10rem;
  }
}

@media screen and (max-width: 750px) {
  .media-text-section--container {
    padding: 2.5rem 2rem !important;
  }
  .media-text-section--wrapper {
    flex-direction: column-reverse;
  }
  .text-content-wrapper {
    text-align: center;
    width: 100%!important;
  }

  .media-container {
    width: 100%!important;
  }
  .media-text-section--wrapper {
    gap: 2rem!important;
  }

  .media--small .section-ordered .text-content-wrapper, 
  .media--medium .section-ordered .text-content-wrapper {
    padding: 0
  }

  .text-content-wrapper {
    padding: 0 2rem;
  }

  .media--large-vertical .media-wrapper .ButtonGroup--controlers {
    bottom: initial;
    top: 2rem;
  }

  .media--large-vertical .media-wrapper {
    padding-bottom: 177%;
  }

  .media-wrapper video {
    width: max(350%, 250vh);
  }

  .media--large-vertical .text-content-wrapper .text-small,
  .media--large-vertical .text-content-wrapper .text,
  .media--large-vertical .text-content-wrapper .text-large {
    max-width: 100%;
    margin: 1.5rem 0;
  }

  .media--large-vertical .text-content-wrapper .Button {
    margin-top: 0;
  }
}