@use '../../common/styles/mixins' as *;

.quick-shop {
  position: relative;
  padding-top: 3rem;

  @include respond(mid) {
    padding-bottom: 3rem;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__head {
    text-align: center;
    line-height: normal;
    margin-bottom: 3.5rem;
    text-transform: uppercase;
  }

  &__body {
    &:not(.swiper-container-initialized) .swiper-wrapper {
      display: flex;
    }

    &:not(.swiper-container-initialized) &__card {
      width: 23%;
      @include respond(mid) { width: 47%; }
    }

    &--wrapper {
      position: relative;
    }

    &--hover-navigation {
      position: absolute;
      top:0;
      width: 14%;
      height: 100%;
      z-index: 2;
    }
    &--prev {
      left: 0;
      @media screen and (max-width: 1100px) {
        display: none;
      }
    }
    &--next {
      right: 0;
      @media screen and (max-width: 1100px) {
        display: none;
      }
    }
  }

  &__main-title {
    font-size: 2rem;
    font-weight: 500;
    margin: 0;
    margin-bottom: 1.5rem;
  }
  
  &__link {
    font-size: 1.4rem;
    padding-bottom: .3rem;    
  }

  &__loader {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 0.5rem;

    @include respond(mid) {
      grid-template-columns: 1fr 1fr;
      
      .placeholder-loader:nth-child(2n) {
        display: none;
      }
    }
  }

  &__main-title,
  &__link {
    line-height: inherit;
    text-transform: inherit;
  }

  &__card {
    position: relative;
    margin-right: .5rem;

    svg { transition: transform .3s ease; }

    .product-item__footer { min-height: 7rem; }

    .wishlist-engine {
      @media screen and (min-width: 1140px) {
        opacity: 0; 
      }
      position: absolute;
      top: 1.5rem!important;
      left: 1.5rem!important;
      z-index: 9;
      margin-top: -3px;
      margin-left: -5px;
      @media screen and (max-width: 485px) {
        top: .5rem;
        left: .5rem;
        .wishlist-engine-butto {
          svg {
            width: 1.6rem;
            height: 1.6rem;
          }
        }
      }
    
      .wishlist-engine-button {
        background-color: transparent!important;
      }
    } 

    &:hover {
      .wishlist-engine {
        opacity: 1!important;
      }
    }

    &.active {
      svg {
        transform: rotate(225deg);
        width: 2rem;
        height: 2rem;
      }
      .wishlist-engine {
        opacity: 0!important;
      }

      .product-item__cart-btn {
        color: transparent;
      }

      @media screen and (max-width: 1100px) {
        .product-item__footer { display: none; }
      }
    }
  }
  
  &__body.enable-transition &__card {
    transition: all .3s !important;
    transition-property: width, transform !important;
  }

  &[data-simplified="false"] .product-item__cart-btn {
    top: 2rem;
    right: 2rem;
  }

  &__product-content {
    position: absolute;
    bottom: 9.65rem;
    right: 0;
    z-index: 3;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    padding: 0 2rem;
    transition: opacity .001s;
    transition-delay: .3s;

    @media screen and (max-width: 1100px) {
      position: static;
      display: none;
      margin-top: 2rem;
    }

    &.active {
      opacity: 1;
      visibility: visible;
      display: flex;
    }
  }

  &__price,
  &__product-title {
    font-size: 2.4rem;
    font-weight: 500;
    line-height: 2.8rem;
    text-align: center;

    @media screen and (max-width: 1200px) {
      font-size: 2.2rem;
    }
  }

  &__product-title {
    margin-bottom: 2rem;
  }

  &__content-head {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1rem;
  }

  &__content-body {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__color-label {
    margin-bottom: 3rem;
    text-transform: uppercase;
    font-size: 1.4rem;

    @media screen and (max-width: 1200px) {
      margin-bottom: 1.5rem;
    } 
  }

  &__content-footer {
    display: flex;

    @include respond(mid) {
      .Button { padding: 1rem 2rem; }
    }
    
    & > *:not(:last-child) { margin-right: 1.5rem; }
  }

  &__options-list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__option {
    ul {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      &.disabled { opacity: .5; }
    }

    &:not(last-child) {
      margin-bottom: 3rem; 
      @media screen and (max-width: 1200px) {
        margin-bottom: 1.5rem; 
      }
    }
  }

  &__option ul.disabled &__option-item {
    cursor: default;
  }

  &__option-item {
    display: flex;
    position: relative;
    margin-bottom: .5rem;

    img {
      width: 2rem;
      height: 2rem;
      object-fit: cover;
      position: relative;
      z-index: 2;
    }

    &:not(:last-child) { margin-right: 1rem; }

    &--COLOR.active::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 2.45rem;
      height: 2.45rem;
      z-index: 1;
      background: #fff;
      border: 1.5px solid #000;
    }

    &:not(.quick-shop__option-item--COLOR) {
      padding: .5rem 2.25rem;
      font-size: 1.2rem;
      border: 1.5px solid rgba(#000, .65);
      text-transform: uppercase;

      &.active {
        background: #000;
        color: #fff;
      }
    }
  }

  &__btn {
    padding: 1rem calc(100vw / 40)!important;
  }
}
.image-slide {
  .image-wrapper {
    position: relative;
    height: 0;
    padding-bottom: 135%;
    width: 100%;
    
    img {
      position: absolute;
      top:0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &--link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &--footer {
    margin-top: .5rem;
    padding: 0 1rem;
  }
}