
.blocks-wrapper {
  display: flex;
  justify-content: center;
}

.main-carousel {
  display: flex;
  gap: 2rem;
  padding-left: 2rem;
  padding-right: 2rem;;
}
.main-carousel.block-full {
  gap: 0!important;
  padding-left: 0;
  padding-right: 0;
}
.main-carousel.carousel--2 {
  gap: 4rem;
}

.block-inner {
  display: inline-block;
  position: relative;
  vertical-align: bottom;

  .ButtonGroup--Btn {
    bottom: 2rem;
  }

  .ButtonGroup--left {
    &.ButtonGroup--playToggle {
      left: 2rem;
      right: initial;
    }
    &.ButtonGroup--muteToggle {
      left: 2rem;
      right: initial;
      transform: translateX(100%);
    }
    @media screen and (max-width: 750px) {
      &.ButtonGroup--playToggle {
        left: 1.5rem;
      }
      &.ButtonGroup--muteToggle {
        left: 1.5rem;
      }
    }
  }
  
  .ButtonGroup--right {
    &.ButtonGroup--playToggle {
      right: 2rem;
      left: initial;
      transform: translateX(-100%);
    }
    &.ButtonGroup--muteToggle {
      right: 2rem;
      left: initial;
    }
    @media screen and (max-width: 750px) {
      &.ButtonGroup--playToggle {
        right: 1.5rem;
      }
      &.ButtonGroup--muteToggle {
        right: 1.5rem;
      }
    }
  }
  
  .ButtonGroup--center {
    &.ButtonGroup--playToggle {
      left: 2rem;
    }
    &.ButtonGroup--muteToggle {
      right: 2rem;
    }
    @media screen and (max-width: 750px) {
      &.ButtonGroup--playToggle {
        left: 1.5rem;
      }
      &.ButtonGroup--muteToggle {
        right: 1.5rem;
      }
    }
  }
}

.block-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 2;
  & > * {
    text-align: center;
    color: #fff!important
  }
}

.block-seperated .block-content {
  width: 100%;
  top: 50%;
  left: 50%;
  bottom: initial;
  transform: translate(-50%, -50%);
  align-items: center;
  padding: 0 6rem;

  @media screen and (max-width: 750px) {padding: 0 4rem;}
  @media screen and (max-width: 500px) {padding: 0 2rem;}
}

.block-content {
  h2 {
    margin: 0;
    text-transform: uppercase;
    letter-spacing: var(--heading-letter-spacing);
    @media screen and (max-width: 750px) {
      letter-spacing: var(--heading-letter-spacing-mobile);
    }
  }
  .text {
    letter-spacing: var(--text-letter-spacing);
    @media screen and (max-width: 750px) {
      letter-spacing: var(--text-letter-spacing-mobile);
    }
  }
}

.block-seperated .block-content p.text-small,
.block-seperated .block-content p.text,
.block-seperated .block-content p.text-large {
  margin-bottom: 0;
}

.block-seperated .block-content .Button {
  margin-top: 1.5rem;
}

.block-content > p.text-small {
  margin-bottom: 1.5rem;
}

.block-inner img {
  max-width: 100%;
  vertical-align: bottom;

  position: absolute;
  height: 100%;
  object-fit: cover;
}

.block-full .block-inner img {
  width: 100%;
}

.block--2 .block-inner img {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.block--3:first-child .block-inner img {
  right: 0;
}

.block--3:first-child .block-inner .block-content {
  left: initial;
  transform: translate(0, -50%);
}

.block--3:first-child .block-inner::after {
  left: initial;
  right: 0;
  transform: none;
}

.block--3:last-child .block-inner img {
  left: 0;
}

.block--3:last-child .block-inner .block-content {
  right: initial;
  left: 0;
  transform: translate(0, -50%);
}

.block--3:last-child .block-inner::after {
  right: initial;
  left: 0;
  transform: none;
}

.block--3:nth-child(2) .block-inner img {
  left: 50%;
  transform: translateX(-50%);
}




.block-outter.block--3,
.block-outter.block--4 {
  width: 66%!important;
}


.block-inner {
  height: 0;
  padding-bottom: calc(100% / 0.9);
  width: 100%;
}

.block-full .block-inner {
  padding-bottom: 133.33%;
}

.video-wrapper {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: block;
}

.video-wrapper video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.block-outter,
.block-inner img,
.block-inner::after {
  width: 100%
}

.block-inner::after {
  content: "";
  width: 100%; 
  height: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.block-link--wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
}

.block--4 .block-seperated,
.block--4.block-seperated .block-inner::after {
  left: 50%;
  transform: translateX(-50%)
}

.block--4 .block-content {
  padding: 0 2rem;
}

.header-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  padding: 0 2rem;
}

.header-content {
  margin-bottom: 4rem;
}

.header-content p.text-small,
.header-content p.text,
.header-content p.text-large {
  max-width: 50%;
}

.header-content > * {
  margin: 0;
  text-align: center;
}


.flickity-viewport {
  width: 100%;
}

@media screen and (max-width: 840px) {
  .block--4 .block-content {
    padding: 0 1rem;  
  }

  .block-seperated .block-content .text-small, 
  .block-seperated .block-content .text 
  .block-seperated .block-content .text-large { 
    padding: 0
  }
}


@media screen and (max-width: 750px) {

  .blocks-container{
    padding: 2.5rem 0!important; 
  }

  .blocks-wrapper {
    flex-direction: column;
  }

  .blocks-wrapper.block-seperated {
    gap: 2rem;
  }

  .block-outter {
    width: 100%!important;
  }

  .block-content h5 {
    padding: 0;
  }

  .block-inner img {
    left: 50%!important;
    transform: translateX(-50%)!important;
  }

  .main-carousel {
    align-items: center;
    overflow: hidden;
  }

  .block-outter.block--3 {
    flex-shrink: 0;
  }

  .main-carousel.carousel--2 {
    flex-direction: column;
    gap: 2rem;
  }

  .header-content {
    margin-bottom: 3rem;
  }

  .block--3:first-child .block-inner .block-content {
    padding: 0 6rem;
  }

  .header-content p.text-small,
  .header-content p.text,
  .header-content p.text-large {
    max-width: 100%;
  }

  .carousel-cell.block-outter {
    margin-right: 2rem;
  }

  .blocks--container .carousel-cell.block-outter {
    margin-right: 0;
  }

  .carousel-cell.block-outter.block-full {
    margin-right: 0;
  }

  .main-carousel {
    padding-left: 0;
    padding-right: 0;
  }

  .main-carousel.carousel--2.block-seperated {
    padding-left: 2rem;
    padding-right: 2rem;
  }

}

@media screen and (max-width: 400px) {
  .block-content {
    bottom: 2.5rem;
  }

  .block-inner .Button {
    padding: 0.8rem 10.4rem;
  }

  .block--3 .block-inner .block-content {
    padding: 0 1rem!important;
  }
}

.video-image-blocks-container .block-content {
  &--top {
    top: 4rem!important;
    bottom: initial!important;
    left: 50%!important;
    right: initial!important;
    transform: translate(-50%, 0)!important;
    width: 100%;
  }
  &--bottom {
    top: calc(100% - 4rem)!important;
    bottom: initial!important;
    left: 50%!important;
    right: initial!important;
    transform: translate(-50%, -100%)!important;
    width: 100%;
  }
  &--left {
    align-items: flex-start;
    & > * {
      text-align: left;
    }
    .Button {
      padding-left: 0!important;
    }
  }
  &--right {
    align-items: flex-end;
    & > * {
      text-align: right;
    }
    .Button {
      padding-right: 0!important;
      justify-content: right!important;
    }
  }

  .Button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem .5rem;
    min-width: 45%;
    max-width: 100%;
  }
}